@import url("http://fonts.googleapis.com/css?family=Open+Sans:400,600,300");
.main-div {
  font-family: "Open Sans", sans-serif;
  // font-weight: 300;
  text-rendering: optimizeLegibility;
  font-size: 0.9em;
  padding-bottom: 90px;
}

table {
  border: 0px solid !important;

  thead {
    height: 20px !important;
    tr {
      height: 20px !important;

      th {
        z-index: 111111999999999999;
        background: var(--app-color-primary) !important;
        color: #fff;
        border: var(--app-color-primary) !important;
        height: 20px !important;
      }
    }
  }
}

.t-and-c {
  padding-top: 14px;
  width: 100%;
  font-size: 1.7em;
}

.left-float {
  margin-top: 20%;
  max-width: 100%;
}

.stamp-float {
  // position: absolute;
  bottom: 70px;
  left: 150px;
  // background: red;
  width: 310px;
  height: 310px;
  margin-top: -100px;
  margin-left: 220px;

  img {
    width: auto;
    max-width: 310px;
    max-height: 310px;
  }
}

.signature-float {
  margin-top: -102px;
  margin-left: 480px;

  img {
    width: auto;
    max-width: 100px;
    max-height: 100px;
  }
}

.date-float {
  margin-top: -50px;
  margin-left: 3px;
  font-size: 1.7em;
  color: red;
  // width: 140px !important;
  text-align: center;
  background: blue !important;
}

.payment-foot {
  padding-top: 10px;
  span {
    text-transform: uppercase;
  }
}

.auth-sig-float {
  margin-top: 1px;
  margin-left: 20%;

  .auth-rowa {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;

    div {
      &:nth-child(2) {
        width: 180px;
        border-bottom: 1px solid black;
      }
    }
  }
}

.dont-print {
  @media print {
    display: none !important;
  }
}
